import React, { Component } from 'react';
//import { useNavigate } from 'react-router-dom';
import { AppBar, Grid, Toolbar, Typography, Button, Card, Container, CardContent, CardMedia, Stack, Divider } from '@mui/material';
import 'bootstrap/dist/css/bootstrap.min.css';
import './controlR.css';
import F1 from '../Productos/F1.jpg';
